.subject-navbar .nav-pills li {
    padding-right: 15px;
}
.subject-navbar .nav-pills li a {
    color: #262828;
}

.subject-content {
    margin-top: 30px;
}
.subject-content .chapter-wrapper {
    padding: 30px;

    box-shadow: 0px 2px 3px #c7eadd;
    margin-bottom: 10px;
}
.subject-content .chapter-wrapper .chapter-title {
    font-size: 22px;
}
.subject-content .chapter-wrapper .chapter-title a {
    color: black;
    text-decoration: none;
}
.subject-content .chapter-wrapper .chapter-title a:hover {
    color: #25cb83;
}
.subject-content .chapter-wrapper .chapter-title span {
    padding-right: 10px;
    color: #25cb83;
}
.subject-content .chapter-wrapper .option {
    text-align: right;
}
.subject-content .chapter-wrapper .option a {
    display: inline-block;
    padding-right: 30px;
    margin-top: 5px;
    color: #2ad181;
    font-size: 20px;
}
.subject-content .chapter-wrapper .option a:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transition: color 0.3s linear, -webkit-transform 0.3s ease-in-out;
    transition: color 0.3s linear, -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, color 0.3s linear;
    transition: transform 0.3s ease-in-out, color 0.3s linear, -webkit-transform 0.3s ease-in-out;
    color: #009d98;
}
@media screen and (max-width: 768px) {
    .subject-navbar .icon-box {
        font-size: 32px;
        padding: 12px;
    }
    .subject-navbar {
        padding: 60px 0 0 0;
        display: inline-block;
    }
    .subject-navbar.d-flex {
        display: inline-block !important;
    }
    .subject-navbar .back {
        left: 15px;
        top: 15px;
    }
    .subject-content {
        margin-bottom: 45px;
    }
    .subject-content .chapter-wrapper {
        padding: 30px 15px;
    }
    .subject-content .chapter-wrapper .option a {
        padding-right: 0px;
        padding-left: 20px;
    }
    .subject-navbar .nav-pills li {
        padding-right: 5px;
    }
    .subject-content .chapter-wrapper .option {
        display: inline-flex;
    }
}
/* slideshow */
#flash {
    padding: 45px;
}
#flash .chapter-title {
    margin-bottom: 5px;
    margin-top: 20px;
    font-size: 22px;
}
#flash .chapter-title span {
    padding-right: 10px;
    color: #25cb83;
}
@media screen and (max-width: 768px) {
    #flash {
        padding: 0;
    }
}
.carousel-inner .carousel-box {
    padding: 30px;
    background: #f8f8f9;
    color: #655d5d;
    margin: 0px 5px;
}
.top-content .slick-slider button {
    padding: 34px 0px;
    background: linear-gradient(45deg, #2ad181, #009d98);
}
.top-content .slick-next:before,
.top-content .slick-prev:before {
    color: white;
}
.top-content .slick-next,
.top-content .slick-prev {
    width: 30px;
    height: auto;
}
.top-content a {
    text-decoration: none;
    color: #655d5d;
}
.top-content .carousel-control-prev {
    left: -15px;
    background: linear-gradient(45deg, #2ad181, #009d98);
    width: 30px;
    opacity: 1;
}
.top-content .carousel-control-next {
    right: -15px;
    background: linear-gradient(45deg, #2ad181, #009d98);
    width: 30px;
    opacity: 1;
}

/*note viewer page */
